body {
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  max-width: 166px;
  max-height: 134px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
}

.card__title {
  color: #060b35;
  max-width: 166px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 1.4;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews-wrapper {
  color: #060b35;
  justify-content: space-between;
  align-items: flex-end;
  font-size: 10px;
  line-height: 1.4;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 1.5;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  max-width: 166px;
  padding-block: 12px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.14286;
  text-decoration: none;
  transition: all .2s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.stars__star {
  box-sizing: border-box;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 {
  display: flex;
}

.stars--0 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
